<template>
  <v-container>
    <h5 class="my-4">
      <span class="cblu bbred">Listing in {{city}}</span>
    </h5>
    <v-row>
      <v-col cols="12" md="3">
       <CatSidebar/>
      </v-col>
      <v-col cols="12" md="9">
        <div class="mobile" v-if="rlists.length > 1">
          <v-card v-for="(clist,i) in rlists" :key="i" class="item mitem my-2 mb-7" :perPage="perPage">
            <div class="small d-flex">
                <div class="mimg">
                  <router-link :to="{name: 'businessprofile', params: {id: clist.business_id, uname:clist.business_uname}}" class="link-dark">
                      <v-img src="https://bizlx.itechvision.in/images/hot-deal-home-1.jpg" cover class="rounded-3" height="110"></v-img>
                  </router-link>
                </div>
                <div class="mdetails">
                  <router-link :to="{name: 'businessprofile', params: {id: clist.business_id, uname:clist.business_uname}}" class="mtitle text-decoration-none link-dark h5">
                    {{clist.name}}
                  </router-link>
                  <div>Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
                  <div class="mreviews d-flex align-items-center">
                    <v-rating dense color="warning" size="16" length="5" value=4.5></v-rating>
                    <span class="badge bg-dark">4.2</span>
                  </div>
                  <div class="address maddress">
                    <v-icon small>mdi-map-marker</v-icon>
                    {{ clist.address }} {{ clist.city }}, {{ clist.state }}
                  </div>
                  <div>
                    <router-link :to="{name: 'businessprofile', params: {id: clist.business_id, uname:clist.business_uname}}" class="cred">Read More</router-link>
                  </div>
                 </div>
                  <div class="mcontact">
                    <a :href="('https://wa.me/'+clist.mobile_phone+'?text=rf')">
                      <v-btn fab small color="success" class="mb-5"><v-icon>mdi-whatsapp</v-icon></v-btn>
                    </a>
                    <a :href="('tel:'+ clist.mobile_phone)">
                      <v-btn fab small color="primary"><v-icon>mdi-phone</v-icon></v-btn>
                    </a>
                  </div>
                 </div>
        </v-card>
          <v-pagination v-if="rlists.length > 1" class="pagination" v-model="page" :length="Math.ceil(citylists.length/perPage)" circle></v-pagination>
        </div>
        <div class="desktop" v-if="rlists.length > 1">
          <v-card v-for="(clist,i) in rlists" :key="i" class="mb-7" :perPage="perPage">
          <v-row>
            <v-col md="4" class="py-0">
              <router-link :to="{name: 'businessprofile', params: {id: clist.business_id, uname:clist.business_uname}}" class="link-dark">
                 <v-img src="https://bizlx.itechvision.in/images/hot-deal-home-1.jpg" cover height="200"></v-img>
              </router-link>
            </v-col>
            <v-col cols="12" md="8" class="py-0">
              <v-card-text>
                <v-row>
                  <v-col cols="12" md="8">
                    <router-link :to="{name: 'businessprofile', params: {id: clist.business_id, uname:clist.business_uname}}" class="text-decoration-none link-dark h5">
                      {{clist.name}}
                    </router-link>
                    <div>Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
                    <div class="mreviews d-flex align-items-center">
                      <v-rating dense color="warning" size="16" length="5" value=4.5></v-rating>
                      <span class="badge bg-dark">4.2</span>
                    </div>
                    <div class="address">
                      <v-icon small>mdi-map-marker</v-icon>
                      {{ clist.address }} {{ clist.city }}, {{ clist.state }}
                      <router-link :to="{name: 'businessprofile', params: {id: clist.business_id, uname:clist.business_uname}}" class="cred">Read More</router-link>
                    </div>
                  </v-col>
                  <v-col md="4">
                    <div class="align-middle">
                      <a :href="('tel:'+ clist.mobile_phone)">
                        <v-btn small color="primary" class="me-1 mb-2">
                          <v-icon small>mdi-phone</v-icon>Call Now</v-btn>
                      </a>
                      <a :href="('https://wa.me/'+clist.mobile_phone+'?text=rf')">
                        <v-btn small color="success" class="mb-2">
                          <v-icon small>mdi-whatsapp</v-icon>WhatsApp</v-btn>
                      </a>
                    </div>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-col>
          </v-row>
        </v-card>
          <v-pagination v-if="rlists.length > 1" class="pagination" v-model="page" :length="Math.ceil(citylists.length/perPage)" circle></v-pagination>
      </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import CatSidebar from "@/views/CatSidebar";
import axios from "axios";
export default {
  name: "CityListings",
  components: {CatSidebar},
  metaInfo: {title: 'City Listings'},
  props:{
    city:String
  },
  data() {
    return {
      page:1,
      perPage:10,
      citylists:[],
    };
  },
  methods:{
    getCitylistings(){
      axios.get(`citylistings/${this.city}`)
          .then((resp)=>{
            this.citylists = resp.data.citylistings;
          });
    }
  },
  created() {
    this.getCitylistings();
  },
  computed:{
    rlists(){
      return this.citylists.slice((this.page - 1)*this.perPage, this.page*this.perPage)
    }
  }
}
</script>

<style scoped>
.item.mitem {
  box-shadow: 0 6px 10px -6px rgba(0,0,0,0.5);
  background: #f1f2f380;
  padding: 7px;
}
.mimg {
  width: 110px;
}
.mtitle {
  font-weight: 600;
  font-size: 12.25px;
}
.mdetails {
  width: 100%;
  padding: 0px 0 0 7px;
}
.mcontact {
  width: 42px;
}
.fs11{font-size: 10.4px;}

</style>